@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap");

$mobile-small: 575px;
$mobile: 768px;
$tablet: 1024px;
$desktop-small: 1250px;

@mixin mobile-small {
  @media (max-width: $mobile-small) {
    @content;
  }
}

@mixin mobile {
  @media (min-width: $mobile-small) and (max-width: $mobile) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: $mobile) and (max-width: $tablet) {
    @content;
  }
}

@mixin desktop-small {
  @media (min-width: $tablet) and (max-width: $desktop-small) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: $desktop-small) {
    @content;
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.hero-temp {
  pointer-events: none;
  overflow: hidden;
  // height: 86vh;
}

.landing-header {
  position: absolute;
  font-family: "Poppins", sans-serif;
  font-size: 4vw;
  margin-left: 0.4em;
  padding-top: 0.3em;

  #potentia {
    padding-left: 0.4em;
    z-index: 999 !important;
    position: relative;
  }

  #robotics {
    position: relative;
    top: -0.3em;
    padding-left: 1.35em;
    z-index: 999 !important;
  }
}

.landing-robot {
  position: absolute;
  opacity: 1;
  // left: 40vw;
  overflow: hidden;
  // animation: navLinkFade 0.5s ease forwards 0.5s;
  // max-height: 87vh;
  width: 100%;
}

@keyframes robotFlyIn {
  from {
    transform: translateX(150vw);
    opacity: 0;
  }

  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.highlight-gray {
  background-color: rgb(235, 235, 235);
}

.landing-subheader {
  font-family: "Poppins", sans-serif;
  font-size: 2vw;
  position: absolute;
  margin-top: 55vh;
  // margin-right: 10%;
  // right: 0;
  margin-left: 4em;
  z-index: 999 !important;
  width: 45%;

  @include desktop-small {
    margin-top: 40vh;
  }

  @include tablet {
    margin-top: 33vh;
  }

  @include mobile {
    margin-top: 25vh;
  }

  @include mobile-small {
    margin-top: 17vh;
  }

  a {
    color: inherit;
    text-decoration: none;
    pointer-events: all;
    &:hover {
      text-decoration: underline;
    }
  }
}
